import React from 'react';

export default class Jokes extends React.Component {
    constructor(props) {
        super(props);

        this.header = "My favorite Jokes"
        this.text = "I have a really good knock knock joke but you gotta start it"
        this.state = {
            open: false,
            toggleShow: this.props.toggleShow
        }
    }

    handleClick  = () => {
        this.props.toggleShow(true, this.header, this.getHtmlContent());
    }

    getHtmlContent() {
        return(
            <div className="return-value">
                <p>{this.text}</p>
                <p>Send me your best Jokes</p>
            </div>
        )
    }

    render() {
        return(
            <div className="content">
                <div className="col">
                <button className="btn btn-block" type="button" onClick={this.handleClick}>
                    <strong>Top X Jokes</strong>
                </button>
                </div>
            </div>
        )}
}